/**
 * File: src/services/maskSystem.js
 * Purpose: Handle mask application for dual-type cards using Fabric.js
 * Dependencies:
 *   - fabric.js library
 *   - src/utils/maskTemplates.js (imported for default masks)
 *   - Used by Canvas.jsx and layerManager.js
 * 
 * This service replaces the CSS mask system from the original project
 * with a Fabric.js compatible solution using clipPath objects.
 */

import { fabric } from 'fabric';
import { getMaskPreview } from '../utils/maskTemplates';

/**
 * Apply a mask to a Fabric.js object
 * @param {Object} targetObject - Fabric.js object to mask
 * @param {string} maskUrl - URL to mask image (or empty for default clip path)
 * @returns {Promise<Object>} Object with properties to apply to target
 */
export const applyMask = (targetObject, maskUrl) => {
  // Use default diagonal clip-path if no mask specified
  if (!maskUrl || maskUrl === '') {
    return {
      clipPath: new fabric.Polygon({
        points: [
          { x: 0, y: 0 },
          { x: targetObject.width, y: 0 },
          { x: 0, y: targetObject.height }
        ],
        absolutePositioned: true
      })
    };
  }
  
  // Use image mask
  return new Promise((resolve) => {
    fabric.Image.fromURL(maskUrl, (img) => {
      img.scaleToWidth(targetObject.width);
      img.scaleToHeight(targetObject.height);
      
      resolve({
        clipPath: img,
        clipTo: null
      });
    });
  });
};

/**
 * Get all available masks for the mask selector
 * @returns {Array} Array of mask objects with id, name, and previewUrl
 */
export const getAvailableMasks = () => {
  return [
    { id: '', name: 'Default Diagonal', previewUrl: getMaskPreview('default') },
    { id: 'img/masks/mask1.png', name: 'Horizontal Split', previewUrl: getMaskPreview('horizontal') },
    { id: 'img/masks/mask2.png', name: 'Vertical Split', previewUrl: getMaskPreview('vertical') },
    { id: 'img/masks/mask3.png', name: 'Wave Pattern', previewUrl: getMaskPreview('wave') },
    { id: 'img/masks/mask4.png', name: 'Circle Blend', previewUrl: getMaskPreview('circle') },
    { id: 'img/masks/mask5.png', name: 'Flame Pattern', previewUrl: getMaskPreview('flame') },
    { id: 'img/masks/mask6.png', name: 'Zig-Zag', previewUrl: getMaskPreview('zigzag') },
    // Add more masks as needed
  ];
};

/**
 * Apply a mask effect to a canvas for preview purposes
 * @param {HTMLCanvasElement} canvas - Canvas element to apply effect to  
 * @param {string} maskUrl - URL to mask image
 * @param {string} primaryColor - CSS color string for primary color
 * @param {string} secondaryColor - CSS color string for secondary color
 */
export const createMaskPreview = (canvas, maskUrl, primaryColor, secondaryColor) => {
  const ctx = canvas.getContext('2d');
  const width = canvas.width;
  const height = canvas.height;
  
  // Fill with primary color
  ctx.fillStyle = primaryColor;
  ctx.fillRect(0, 0, width, height);
  
  if (!maskUrl || maskUrl === '') {
    // Draw default diagonal clip-path preview
    ctx.fillStyle = secondaryColor;
    ctx.beginPath();
    ctx.moveTo(0, 0);
    ctx.lineTo(width, 0);
    ctx.lineTo(0, height);
    ctx.closePath();
    ctx.fill();
    return;
  }
  
  // Load mask image
  const maskImg = new Image();
  maskImg.onload = () => {
    // Set secondary color
    ctx.fillStyle = secondaryColor;
    
    // Apply mask using globalCompositeOperation for preview
    ctx.globalCompositeOperation = 'destination-in';
    ctx.drawImage(maskImg, 0, 0, width, height);
    
    // Reset composite operation
    ctx.globalCompositeOperation = 'source-over';
  };
  
  maskImg.src = maskUrl;
};