/**
 * File: src/utils/maskTemplates.js
 * Purpose: Provide utilities for mask previews and templates
 * Dependencies:
 *   - Used by MaskSelector.jsx
 * 
 * This utility provides functions to generate and retrieve
 * mask previews for the mask selection UI.
 */

// Map of mask IDs to preview image URLs
const maskPreviews = {
    'default': 'assets/maskPreviews/default.png',
    'horizontal': 'assets/maskPreviews/horizontal.png',
    'vertical': 'assets/maskPreviews/vertical.png',
    'wave': 'assets/maskPreviews/wave.png',
    'circle': 'assets/maskPreviews/circle.png',
    'flame': 'assets/maskPreviews/flame.png',
    'zigzag': 'assets/maskPreviews/zigzag.png'
  };
  
  /**
   * Get preview image URL for a mask
   * @param {string} maskId - Identifier for the mask
   * @returns {string} URL to preview image
   */
  export const getMaskPreview = (maskId) => {
    return maskPreviews[maskId] || maskPreviews['default'];
  };
  
  /**
   * Generate a simple preview image for a mask (used as fallback)
   * @param {string} maskId - Mask identifier
   * @param {string} primaryColor - Primary type color
   * @param {string} secondaryColor - Secondary type color 
   * @returns {string} Data URL for generated preview
   */
  export const generateMaskPreview = (maskId, primaryColor = '#6390F0', secondaryColor = '#EE8130') => {
    // Create a canvas element
    const canvas = document.createElement('canvas');
    canvas.width = 120;
    canvas.height = 160;
    
    const ctx = canvas.getContext('2d');
    
    // Fill with primary color
    ctx.fillStyle = primaryColor;
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    
    // Simple mask rendering based on maskId
    switch (maskId) {
      case 'horizontal':
        // Horizontal split
        ctx.fillStyle = secondaryColor;
        ctx.fillRect(0, 0, canvas.width, canvas.height / 2);
        break;
        
      case 'vertical':
        // Vertical split
        ctx.fillStyle = secondaryColor;
        ctx.fillRect(0, 0, canvas.width / 2, canvas.height);
        break;
        
      case 'circle':
        // Circle blend
        ctx.fillStyle = secondaryColor;
        ctx.beginPath();
        ctx.arc(canvas.width / 2, canvas.height / 2, canvas.width / 3, 0, Math.PI * 2);
        ctx.fill();
        break;
        
      case 'zigzag':
        // Zigzag pattern
        ctx.fillStyle = secondaryColor;
        ctx.beginPath();
        ctx.moveTo(0, 0);
        ctx.lineTo(canvas.width, 0);
        ctx.lineTo(0, canvas.height);
        ctx.closePath();
        ctx.fill();
        break;
        
      case 'flame':
        // Simple flame-like pattern
        ctx.fillStyle = secondaryColor;
        ctx.beginPath();
        ctx.moveTo(0, 0);
        ctx.quadraticCurveTo(canvas.width / 2, canvas.height / 2, 0, canvas.height);
        ctx.lineTo(0, 0);
        ctx.fill();
        break;
        
      case 'wave':
        // Wave pattern
        ctx.fillStyle = secondaryColor;
        ctx.beginPath();
        ctx.moveTo(0, canvas.height / 2);
        
        // Draw wavy line
        for (let x = 0; x < canvas.width; x += 10) {
          const y = (canvas.height / 2) + Math.sin(x / 10) * 20;
          ctx.lineTo(x, y);
        }
        
        // Complete the path
        ctx.lineTo(canvas.width, 0);
        ctx.lineTo(0, 0);
        ctx.closePath();
        ctx.fill();
        break;
        
      default:
        // Default diagonal split
        ctx.fillStyle = secondaryColor;
        ctx.beginPath();
        ctx.moveTo(0, 0);
        ctx.lineTo(canvas.width, 0);
        ctx.lineTo(0, canvas.height);
        ctx.closePath();
        ctx.fill();
    }
    
    // Return data URL
    return canvas.toDataURL('image/png');
  };
  
  /**
   * Get a list of all available masks
   * @returns {Array} Array of mask objects with id, name, and description
   */
  export const getAllMasks = () => {
    return [
      { 
        id: '', 
        name: 'Default Diagonal', 
        description: 'Classic diagonal split between types'
      },
      { 
        id: 'img/masks/mask1.png', 
        name: 'Horizontal Split', 
        description: 'Horizontal division between types'
      },
      { 
        id: 'img/masks/mask2.png', 
        name: 'Vertical Split', 
        description: 'Vertical division between types'
      },
      { 
        id: 'img/masks/mask3.png', 
        name: 'Wave Pattern', 
        description: 'Flowing wave-like division'
      },
      { 
        id: 'img/masks/mask4.png', 
        name: 'Circle Blend', 
        description: 'Circular blend between types'
      },
      { 
        id: 'img/masks/mask5.png', 
        name: 'Flame Pattern', 
        description: 'Flame-like integration of types'
      },
      { 
        id: 'img/masks/mask6.png', 
        name: 'Zig-Zag', 
        description: 'Zig-zag pattern between types'
      }
    ];
  };